.offers {
  &__container--single {
    background-color: $white;
    margin-bottom: 10px;
    margin-top: 0px;
    padding-bottom: 10px;
    @include respond-to('desktop') {
      height: 100%;
      margin-left: 50px;
      margin-right: 50px;
      margin-top: 20px;
    }

  }
  &__wrapper--single {
    @include respond-to('desktop') {
      margin-left: 50px;
    }
  }
  &__coupon-wrapper--single {
    @include respond-to('desktop') {
      background: transparent;
      margin-bottom: 60px;
      margin-top: -50px;
      // margin-right: 15%;
      // padding: 0 20px;
      // width: 30%;
    }
  }
  &__coupon--single {
    background: $color-orange-light;
    padding: 15px;
    @include respond-to('desktop') {
      margin-top: 15px;
    }
  }
}

.container-flex {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  a:first-child {
    margin-bottom: 20px;
  }
}


.coupon-popin {
  align-items: center;
  background: $white;
  display: flex;
  flex-direction: column;
  margin: 20px auto;
  max-width: 500px;
  padding: 20px;
  position: relative;

  &__element {
    align-items: center;
    display: flex;
    flex-direction: column;
    text-align: center;
    li {
      padding-bottom: 5px;
      text-align: left;
    }
  }
  .overlapping-section__header {
    padding-bottom: 0;
  }
}

.coupon--prevent-print {
  @extend .coupon-popin;
}

.resent-email,
.rest-password-step-1,
.rest-password-step-3 {
  @extend .coupon-popin;

}
