.coupon__box {
  align-items: center;
  //background: $white !important;
  background: $color-orange-light;
  border: dashed $white;
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: center;
  padding: 10px 0;
  position: relative;
}

.coupon__border-pic {
  bottom: 20%;
  display: none;
  position: absolute;
  right: -19px;
  @include respond-to('desktop') {
    img {
      height: 35px;
      width: 35px;
    }
  }
}

.coupon__text {
  color: $color-pink-dark;
  font-family: $font-title-paragraph;
  font-size: 60px;
  line-height: 0.85;
  margin: 0;
  text-align: center;
  text-transform: uppercase;
  span {
    display: block;
  }
  
  @include respond-to('desktop') {
    font-size: 70px;
    padding-bottom: 10px;
  }
  &--bold {
    font-family: $font-title-bold;
    font-size: 80px;
    @include respond-to('desktop') {
      font-size: 100px;
    }
  }
  &--sample-bold {
    font-family: $font-title-bold;
    font-size: 32px;
    padding-bottom: 10px;
    
  }
  &--sample {
    font-size: 50px;
    padding-top: 10px;
  }
}

:lang(nl) {
  .coupon__text {
    &--sample {
      font-size: 28px;
      padding-bottom: 10px;
      padding-top: 10px;
      @include respond-to('desktop') {
        padding-bottom: 10px;
        padding-top: 20px;
      }
    }
    &--sample-bold {
      font-size: 17px;
      padding-bottom: 10px;
      padding-top: 10px;
      word-break: break-word;
      @include respond-to('desktop') {
        padding-bottom: 20px;
        padding-top: 20px;
        
      }  
    }
  }
}