$banner-background: #c3146f;
$banner-height: 85px;
.offers-list {
  margin: 0 auto;
  max-width: 380px;
  @include respond-to('desktop') {
    max-width: 100%;
  }
}

.offers {
  &-container {
    padding: 0 0 35px;
    /* Dots */
    .slick-dots {
      padding: 0;
      @include respond-to('desktop') {
        padding: 0 10% 0 0;
      }
      li.slick-active button::before {
        color: #f7901c;
        font-size: 40px;
      }
      li button::before {
        font-size: 40px;
      }
    }
    @include respond-to('desktop') {
      padding: 0 0 35px 100px;
      .slick-prev {
        left: -80px;
        top: 60%;
      }
      .slick-next {
        right: 25px;
        top: 60%;
      }
    }
  }
  &__header {
    color: $block-color;
    padding: 10px 0;
    text-align: center;
    @include respond-to('desktop') {
      padding: 25px 0 50px;
    }
  }
  &__title {
    font-family: $font-title-paragraph;
    font-weight: 400;
    margin: 0;
  }
  &__subtitle {
    font-family: $font-title-bold;
  }
  &__banner {
    display: none;
    @include respond-to('desktop') {
      background-color: $banner-background;
      display: block;
      height: $banner-height;
      margin-left: 50px;
    }
  }
  &__items-wrapper {
    display: block;
    @include respond-to('desktop') {
      display: flex;
      justify-content: space-around;
      margin-bottom: -135px;
      position: relative;
      top: -$banner-height;
    }
  }
  &__packshot {
    display: none;
    @include respond-to('desktop') {
      display: block;
      position: relative;
      top: -50px;
      width: 35%;
      img {
        height: auto;
        max-width: 100%;
        width: auto;
      }
    }
  }
  &__packshot--sample {
    @include respond-to('desktop') {
      width: 20%;
    }
  }
  &__description {
    display: none;
    @include respond-to('desktop') {
      align-items: center;
      color: $color-grey;
      display: flex;
      padding: 1px;
      width: 20%;
      p {
        font-size: 18px;
        line-height: 1.2;
        margin-top: -10px;
      }
      
    }
  }
  &__description--sample {
    @include respond-to('desktop') {
      width: 27%;
    }
  }
  &__coupon-wrapper {
    @include respond-to('desktop') {
      background: $white;
      margin-right: 15%;
      padding: 0 20px;
      width: 30%;
    }
  }
  &__coupon {
    height: 230px;
    margin: 0 auto;
    max-width: 230px;
    .coupon__box .btn {
      //background-color: $white;
      align-items: center;
      color: $color-pink-dark;
      display: flex;
      font-size: 10px;
      font-weight: bold;
      height: 40px;
      justify-content: center;
      text-align: center;
      width: 125px;
      @include respond-to('desktop') {
        font-size: 13px;
        height: 50px;
        width: 170px;
      }
      &:hover {
        background-color: #f81161;
        border: 2px solid $white;
        color: $white;
        padding-left: 0px;
        padding-right: 0px;
      }
    }
    // .coupon__box .print-coupon {
    //   &:hover {
    //     background-color: $white;
    //     color: $color-pink-light;
    //   }
    // }
    @include respond-to('desktop') {
      height: 100%;
      max-height: 280px;
      max-width: 280px;
    }
  }
}

:lang(nl) {
  .offers__title,
  .offers__subtitle {
    font-size: 28px;
  }
}

:lang(fr) {
  .offers__description {
    p {
      font-size: 13px;
    }
  }
}