// Include your Fonts here

$font-normal: Arial, sans-serif;
$sans-font-family: 'ITCAvantGardeStd-Bk', Arial, sans-serif !default;
$serif-font-family: 'Trebuchet MS', 'Times New Roman', Times, serif !default;

$default-font-family: $sans-font-family !default;
$font-title-bold: 'ITCAvantGardeStd-Bold', Arial, sans-serif;
$font-title-paragraph: 'ITCAvantGardeStd-Bk', Arial, sans-serif;
$font-link: 'ITCAvantGardeW01-CnMd', Arial, sans-serif;
$font-paragraph: 'ITCAvantGardeStd-Bk', Arial, sans-serif;
$font-standart-condensed: 'ITCAvantGardeStd-BkCn', Arial, sans-serif;
$font-standart-condensed-bold: 'ITCAvantGardeStd-BoldCn', Arial, sans-serif;


@font-face {
  font-family: 'AvantGardeGothicITCW02Md';
  src: url('./../fonts/32A33A_C_0.eot');
  src: url('./../fonts/32A33A_C_0.eot?#iefix') format('embedded-opentype'),
    url('./../fonts/32A33A_C_0.woff2') format('woff2'),
    url('./../fonts/32A33A_C_0.woff') format('woff'),
    url('./../fonts/32A33A_C_0.ttf') format('truetype'),
    url('./../fonts/32A33A_C_0.svg#wf') format('svg');
}

