$product-info-height: 86px;

.product-list-container {
  margin-top: 10px;
  padding-top: 100px;

  @include respond-to('desktop') {
    margin-top: 0;
    padding-top: 0;
  }
}

.product-banner {
  margin-top: 103px;
}

.product-detail-info-container {
  background: transparent;
  margin-top: 50px;
  position: relative;

  @include respond-to('desktop') {
    display: flex;
    justify-content: flex-end;
    margin: 0;
    min-height: 250px;
    padding: 0;
  }
}

.product-info__zoom {
  cursor: pointer;
  left: 10%;
  position: absolute;
  top: 95%;
  visibility: visible;
  z-index: 5;

  @include respond-to('desktop') {
    left: 20%;
  }
}

.product-detail-container {
  background: $white;
  padding: 0 25px;

  @include respond-to('desktop') {
    padding: 100px 50px 0;
  }
}

.product-info__carousel {
  height: 265px;
  margin-bottom: -50px;
  position: relative;
  visibility: hidden;
  z-index: 2;

  @include respond-to('desktop') {
    margin-top: -160px;
    width: 50%;
  }
}

.product-info__ratings {
  background: $white;
  padding-top: 100px;

  @include respond-to('desktop') {
    background: transparent;
    padding: 0;
    width: 25%;
  }
}

.product-info__coupon-wrapper {
  background: $white;

  @include respond-to('desktop') {
    background: transparent;
    height: 255px;
    width: 25%;
  }
}

.product-info__coupon {
  margin: 0 auto;
  width: 180px;

  .btn--coupon {
    border: 1px solid #666;
    font-size: 10px;
    line-height: 1.3;
    padding: 8px 10px;

    &:hover {
      padding: 8px 10px;
    }
  }

  @include respond-to('desktop') {
    background: $white;
    height: 100%;
    margin-left: 50px;
    padding: 12px 25px;
    position: relative;
    right: 50px;
    width: auto;
  }
}

.product-details-container {
  background: transparent;
}

.product-info__item-image {
  display: block;
  height: auto;
  margin: 0 auto;
  width: 75%;

  @include respond-to('desktop') {
    width: 375px;
  }
}

.product-description-container {
  padding-top: 0;

  @include respond-to('desktop') {
    padding-top: 100px;
  }
}
