$slider-size: 32px;
$slider-top: 123%;

.slider {
  &__wrapper {
    border: 2px solid $color-grey;
    border-radius: 50px;
    margin-bottom: 45px;
    padding: 2px;
    .slider {
      background-color: transparent;
      height: $slider-size;
      margin: 0;
    }
  }
  &-handle {
    background-color: #8495b9;
    border-radius: 50%;
    height: $slider-size;
    width: $slider-size;
    &:focus {
      outline: 0;
    }
    &:hover {
      background-color: #8495b9;
      @include respond-to('desktop') {
        background-color: #14679e;
      }
    }
  }
  &-handle.active {
    background-color: $form-color;
  }
  &-fill {
    background-color: transparent;
  }
  &__tooltip {
    position: absolute;
    right: 5px;
    text-align: center;
    top: $slider-top;
    width: 25px;
  }
  &__dots {
    display: flex;
    flex-wrap: nowrap;
  }
  &__captions {
    display: flex;
    flex-wrap: nowrap;
    font-size: 10px;
    margin: 8px 0;
    padding-left: 0;

    @include respond-to('all-tablets') {
      font-size: 16px;
    }
    @include respond-to('desktop') {
      font-size: 16px;
    }
  }
  &__dot {
    color: #cecece;
    display: flex;
    flex-grow: 1;
    font-size: 32px;
    i {
      position: relative;
    }
  }
  &__caption {
    display: flex;
    flex-grow: 1;
    max-width: 100%;
    text-align: center;
    top: $slider-top;
    width: 25%;
    span {
      position: relative;
    }
  }
}

$dots-range: (2: 2, 3: 3, 4: 4, 5: 5);
$caption-offset: 4.5%;

@each $key, $value in $dots-range {
  .slider__dots[data-dots='#{$key}'] {
    @if $key == 5 {
      span:nth-child(2):not(:last-child) {
        justify-content: center;
        i {
          right: 17%;
        }
      }
      span:nth-child(3):not(:last-child) {
        justify-content: center;
      }
      span:nth-child(4):not(:last-child) {
        justify-content: center;
        i {
          left: 17%;
        }
      }
    }

    @if $key == 4 {
      span:nth-child(2):not(:last-child) {
        justify-content: center;
        i {
          right: 14%;
        }
      }
      span:nth-child(3):not(:last-child) {
        justify-content: center;
        i {
          left: 14%;
        }
      }
    }

    @if $key == 3 {
      span:nth-child(2):not(:last-child) {
        justify-content: center;
      }
    }
    span:first-child {
      justify-content: flex-start;
    }
    span:last-child {
      justify-content: flex-end;
    }
  }
  .slider__captions[data-captions='#{$key}'] {
    @if $key == 5 {
      li:nth-child(2):not(:last-child) {
        justify-content: center;
        span {
          right: 17%;
        }
      }
      li:nth-child(3):not(:last-child) {
        justify-content: center;
      }
      li:nth-child(4):not(:last-child) {
        justify-content: center;
        span {
          left: 17%;
        }
      }
    }

    @if $key == 4 {
      li:nth-child(2):not(:last-child) {
        justify-content: center;
        span {
          right: 14%;
        }
      }
      li:nth-child(3):not(:last-child) {
        justify-content: center;
        span {
          left: 14%;
        }
      }
    }

    @if $key == 3 {
      li:nth-child(2):not(:last-child) {
        justify-content: center;
      }
    }
    li:first-child {
      justify-content: flex-start;
      text-align: left;
    }
    li:last-child {
      justify-content: flex-end;
      text-align: right;
    }
  }
}

// Ajustement css for NL and DE :lang()
:lang(de),
:lang(nl) {
  .slider__captions {
    letter-spacing: -1px;

    @include respond-to('desktop') {
      letter-spacing: normal;
    }
  }
}