.overlapping-section {
  margin-bottom: 20px;
  margin-top: -50px;
  position: relative;
  @include respond-to('desktop') {
    margin-top: -200px;
  }
  &--small {
    @include respond-to('desktop') {
      margin-bottom: 25px;
      margin-left: auto;
      margin-right: auto;
      margin-top: -200px;
      width: 820px;
    }
  }
  &--high {
    margin-top: -100px;
    @include respond-to('desktop') {
      margin-top: -200px;
    }
  }
  &__header {
    padding: 10px 10px 25px;
    text-align: center;
    @include respond-to('desktop') {
      padding: 10px 100px 50px;
    }
    h1 {
      font-family: $font-title-paragraph;
      line-height: 1;
      font-size: 20px;
      @include respond-to('desktop') {
        font-size: 48px;
      }
      @include respond-to('all-tablets') {
        font-size: 48px;
      }
    }
    span.bold {
      display: block;
      font-family: $font-title-bold;
    }
  }
  &__header--large {
    @include respond-to('desktop') {
      padding: 10px 60px 50px;
    }
  }
  &__introduction {
    margin: 0 auto;
    width: 100%;
    @include respond-to('desktop') {
      width: 675px;
    }
  }
  &__paragraph {
    font-size: 16px;
    margin: 0;
    padding: 10px;
    line-height: 1.4;
    @include respond-to('desktop') {
      font-size: 24px;
    }
    @include respond-to('all-tablets') {
      font-size: 24px;
    }
    &--bold {
      font-family: $font-title-bold;
    }
    &--small {
      font-size: 16px;
      padding: 0;
      @include respond-to('desktop') {
        font-size: 20px;
      }
    }
  }
  &__link {
    text-decoration: none;
  }
  &__button {
    padding: 0;
    @include respond-to('desktop') {
      padding: 25px 200px;
    }
  }
}

.perfectfit-plus-title {
  align-items: center;
  color: $color-orange;
  display: flex;
  font-size: 15px;
  justify-content: center;
  padding-bottom: 5px;
  padding-left: 15px;
  padding-top: 5px;
  @include respond-to('desktop') {
    font-size: 30px;
  }
  @include respond-to('all-tablets') {
    font-size: 30px;
  }
  img {
    height: 20px;
    width: auto;

    @include respond-to('desktop') {
      height: 48px;
    }

    @include respond-to('all-tablets') {
      height: 48px;
    }
  }
  &--profile {
    img {
      height: 16px;
      width: auto;

      @include respond-to('desktop') {
        height: 35px;
      }

      @include respond-to('all-tablets') {
        height: 35px;
      }
    }
  }
}
