$active-tab-color: #f9911e;

.tabs {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin: 0;
  padding-left: 10px;
  &__title {
    background: none;
    color: #222;
    display: inline-block;
    flex-grow: 1;
    font-size: 15px;
    padding: 10px 15px;
    text-align: center;
    @include respond-to('desktop') {
      font-size: 26px;
    }
    &--active {
      border-bottom: 4px solid $active-tab-color;
      font-family: $font-title-bold;
    }
  }
  &__link {
    color: $color-grey;
    text-decoration: none;
    text-transform: uppercase;
  }
  &__panel {
    display: none;
    padding-top: 10px;
    &--active {
      display: block;
      padding-top: 25px;
    }
  }
}

