$box-desktop-width: 115px;
$box-mobile-width: 64px;
$box-btn-dimension: 43px;
.philosophy-container {
  background: $white;
  padding: 20px;
  @include respond-to('desktop') {
    margin: 0 auto -250px;
    position: relative;
    top: -250px;
    width: 820px;
  }
}

.philosophy__logo {
  text-align: center;
  @include respond-to('desktop') {
    text-align: right;
  }
  img {
    height: auto;
  }
}

.philosophy__description {
  padding-top: 20px;
  text-align: center;
  @include respond-to('desktop') {
    text-align: left;
  }
  p {
    @include respond-to('desktop') {
      font-size: 20px;
    }
  }
}

.philosophy-items {
  display: flex;
}

.philosophy__title {
  font-family: $font-title-bold;
  margin: 0;
  span {
    font-family: $font-title-paragraph;
    font-weight: normal;
    &::before {
      content: ' ';
    }
  }
}

.philosophy-item {
  display: inline-block;
  margin: 20px auto;
}

.box-wrapper {
  text-align: center;
}

.box {
  align-items: center;
  border-radius: 6px;
  display: table-cell;
  height: $box-mobile-width;
  padding: 5px;
  vertical-align: middle;
  width: $box-mobile-width;
  @include respond-to('desktop') {
    height: $box-desktop-width;
    margin: 20px auto;
    width: $box-desktop-width;
  }
  &__title {
    font-size: 23px;
    margin: 10px 0;
  }
  &__btn {
    background: $color-grey-light;
    border: 1px solid transparent;
    border-radius: 6px;
    color: $color-blue;
    display: inline-block;
    font-size: 25px;
    height: $box-btn-dimension;
    padding: 8px 0 0;
    transform: rotate(90deg);
    transition: all 0.5s linear;
    width: $box-btn-dimension;
    &:hover {
      border: 1px solid $color-grey-light;
      color: $color-blue;
      padding: 8px 0 0;
    }
    &--active {
      transform: rotate(270deg);
      @include btn-active-state($white, $color-grey-light);
    }
  }
}

:lang(fr),
:lang(nl) {
  .box__title {
    font-size: 10px;
    @include respond-to('desktop') {
      font-size: 23px;
    }
  }
}
.philosophy-description {
  padding: 0 20px;
  @include respond-to('desktop') {
    padding: 0 40px;
  }
}

.philosophy-description {
  display: none;
}

$philosophy-themes: ( feed: $color-green, move: $color-blue, play: $color-pink);
@each $key,
$color in $philosophy-themes {
  .box--#{$key} {
    background: $color;
  }
  .box__title--#{$key} {
    color: $color;
  }
}
