$block-height: 390px;
$block-color: $color-blue-darker;
$img-block-color-mobile: $white;

.total-five-container {
  @include respond-to('desktop') {
    padding: 35px 80px 100px;
  }
}

.total-five__header {
  color: $block-color;
  padding: 50px 0;
  text-align: center;

  @include respond-to('desktop') {
    padding: 10px 0;
  }
  &-title {
    margin: 0;
  }
  &-subtitle {
    display: block;
    font-family: $font-title-bold;
    font-weight: 400;
    text-transform: uppercase;
  }
}

.total-five__header-title {
  color: $color-blue-darker;
  margin: 0;
}

.total-five__overview {
  display: block;
  position: relative;

  @include respond-to('desktop') {
    // height: $block-height;
    display: flex;
  }
}

.total-five__introduction {
  font-weight: bold;
}

.divider {
  @include respond-to('desktop') {
    border-left: 1px solid $white;
    bottom: 10%;
    left: 50%;
    position: absolute;
    top: 10%;
  }
}

.total-five__img {
  margin-top: -50px;
  position: relative;
  z-index: 2;

  @include respond-to('desktop') {
    align-items: center;
    background: $block-color;
    display: flex;
    margin-top: 0;
    width: 50%
  }
  &--home {
    margin: 0;
    background: $block-color;
  }
}

.total-five__description {
  background: $block-color;
  color: $white;
  margin-top: -50%;
  padding: 50% 30px 20px;
  position: relative;

  @include respond-to('desktop') {
    // height: $block-height;
    margin-top: 0;
    padding: 80px 70px;
    position: static;
    width: 50%
  }
  &--home {
    margin: 0;
    background: $white;
    padding: 20px;
    text-align: center;

    @include respond-to('desktop') {
      padding: 50px;
    }
    .total-five__title {
      color: $color-grey;
      text-align: center;

      span {
        font-family: $font-paragraph;
      }
    }
    .total-five__img {
      margin: 0;
      background: $block-color;
    }
    .total-five__paragraph {
      color: $color-grey;
    }
  }
}

.total-five__title {
  color: $white;
  margin: 0;
  text-align: left;
  text-transform: uppercase;
}

.total-five__paragraph {
  color: $white;

  @include respond-to('desktop') {
    padding: 25px 0;
  }
}

.total-five__carousel {
  display: none;

  @include respond-to('desktop') {
    padding: 0 50px;
  }
}

.total-five__img--waltham {
  @include respond-to('desktop') {
    position: relative;
    img {
      position: absolute;
      right: 5px;
      top: 0px
    }
  }
}

.btn--total-five {
  display: block;
  margin: 0 auto;

  @include respond-to('desktop') {
    display: inline;
    margin: 0;
  }
}

.carousel {
  padding-bottom: 30px;

  @include respond-to('desktop') {
    padding-bottom: 0;
  }
}

.carousel__item {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;

  @include respond-to('desktop') {
    height: 200px;
  }
}

.carousel__item-description {
  align-self: flex-start;
  padding-left: 10%;
}

.carousel__item-title {
  font-family: $font-paragraph;
  font-weight: normal;
  text-align: left;
  text-transform: uppercase;
}

.carousel__item-paragraph {
  padding-right: 10%;

  @include respond-to('desktop') {
    padding-right: 0;
  }
}

.carousel__exit {
  color: $block-color;
  cursor: pointer;
  float: right;
  font-size: 40px;
  margin-top: 10px;
  position: relative;
  right: 0;

  @include respond-to('desktop') {
    right: -50px;
  }
}

hr {
  border-bottom: 4px solid #65646d;
  margin: 1.25em auto;
  width: 60px;
}
