.collapsible-section-container {
  background: transparent;
  margin-bottom: 10px;

  @include respond-to('desktop') {
    background: $color-grey-lighter;
    padding: 0 85px 50px 85px;
  }
}

.section-collapsible {
  background: #f7f7f7;
  cursor: pointer;
  margin-top: 10px;

  @include respond-to('desktop') {
    background: transparent;
    cursor: default;
    margin: 0;
    padding: 0;
  }
}

.collapsible-box {
  padding: 10px 0;

  @include respond-to('desktop') {
    padding: 0 25px;
  }
  &__title {
    font-size: 22px;
    text-align: center;

    &--regular {
      display: block;
      font-family: $font-title-bold;
      font-weight: 400;
      text-transform: uppercase;
    }

    @include respond-to('desktop') {
      font-size: 35px;
    }

    @include respond-to('all-tablets') {
      font-size: 35px;
    }
  }
  &__subtitle {
    font-size: 13px;

    @include respond-to('desktop') {
      font-size: 19px;
    }

    @include respond-to('all-tablets') {
      font-size: 19px;
    }
  }
  label {
    cursor: pointer;
  }
  i {
    display: inline-block;
  }
  &__icon {
    font-size: 35px;
    font-weight: bold;
    height: 35px;
    margin: 0 auto;
    padding: 0;
    text-align: center;
    width: 35px;

    @include respond-to('desktop') {
      display: none;
    }
  }
  &--collapsed {
    cursor: default;
    display: none;
    height: 100%;
    padding: 10px 25px;

    @include respond-to('desktop') {
      display: block !important;
      padding: 0;
    }
  }
  &__close {
    transform: rotate(45deg);
    transition: .3s ease-in;
  }
}

