$black: #000 !default;
$white: #fff !default;
// Shades of blue
$color-blue: #1a61ac;
$color-blue-light: #1152a1;
$color-blue-dark: #1152a1;
$color-blue-darker: #1a3072;
$color-blue-darkest: #0a2c74;
$color-sky-blue: #64b2e3;
//Shades of grey
$color-grey: #6a6c71;
$color-grey-light: #d6d7d9;
$color-grey-medium: #d9dada;
$color-grey-dark: #696b70;
$color-grey-lighter: #f7f7f7;
$color-grey-darkest: #65646d;
//Shades of green
$color-green: #22a636;
//Shades of green
$color-pink: #e50075;
$color-pink-dark: #c3146f;
$color-pink-light: #eca7c7;
// Shades of orange
$color-orange: #f7901c;
$color-orange-light: #f9b232;
$color-orange-fade: #fcd999;

$background-color: $color-grey-light;
$header-background-color: $color-grey-dark;
$button-background-color: $color-grey-light;
$button-background-color-hover: $white;
$button-background-border-hover: $color-grey-medium;

//#Dynamic data-themes
$form-color: #1a3072;
$form-error-color: #c2001d;
$form-success-color: green;

$data-themes: (
  pink: #ce0f69,
  white: #fff
);

@each $key, $color in $data-themes {
  [data-theme='#{$key}'] {
    .product-box__title,
    .thumbnail-box__icon,
    .box-info,
    .btn--themed {
      background: #{$color};
    }
    .coupon__box .coupon__text,
    .close-wrapper i[class^='icon-'],
    .icon-wrapper i[class^='icon-'],
    .select-wrapper--color,
    .slick-dots li.slick-active button::before {
      color: #{$color};
    }
    .slick-dots li button:hover {
      &::before {
        color: #{$color};
      }
    }
  }
}
