.mandatory-info {
  font-style: italic;
  padding-left: 12px;
}

.legal-mentions {
  padding-bottom: 35px;
}

.reset-password {
  display: table-cell;
  height: 50px;
  margin: 0;
  vertical-align: middle;

  @include respond-to('desktop') {
    height: 83px;
    padding-top: 10px;
  }
}

.deactivate-account {
  padding: 25px 0;
}

.return-to-login {
  display: block;
  padding: 25px 0;
}

.field-requirements {
  color: $color-grey;
  display: none;
  font-size: 16px;
  line-height: 1.5;
  padding: 0 20px;
}
.confirm-password {
  @extend .field-requirements;
}

.filled {
  color: $color-grey-light;
}
