.load-more__wrapper {
  color: $color-grey;
  position: relative;
  text-align: center;
  top: -50px;
}

.load-more__icon {
  border: 3px solid #666;
  border-radius: 50%;
  cursor: pointer;
  display: block;
  font-size: 20px;
  height: 40px;
  margin: 6px auto;
  opacity: 0.6;
  padding: 6px;
  width: 40px;
}

.load-more__icon:hover {
  opacity: 1;
}
