.text-box {
  &__container {
    background: $white;
    margin-top: -3px;
    padding-left: 20px;
    padding-right: 20px;
    @include respond-to('desktop') {
      margin-bottom: 20px;
      margin-left: 50px;
      margin-right: 50px;
      margin-top: 20px;
      padding-left: 40px;
      padding-right: 40px;
    }
  }
  &__text {
    font-size: 14px;
    text-align: center;
  }
  &__text--left {
    text-align: left;
  }
  &--more-padding {
    padding-bottom: 10px;
    padding-top: 10px;
    display: none;
    @include respond-to('desktop') {
      display: block;
    }
  }
}