// To-do: default config slick

$slick-font-path: './fonts/' !default;
$slick-font-family: 'slick' !default;
$slick-loader-path: './' !default;
$slick-arrow-color: $white !default;
$slick-dot-color: $color-grey !default;
$slick-dot-color-active: $slick-dot-color !default;
$slick-dot-character: '\2022' !default;
$slick-dot-size: 30px !default;
$slick-opacity-default: 0.75 !default;
$slick-opacity-on-hover: 1 !default;
$slick-opacity-not-active: 0.25 !default;
$dot-active-hover-color: $black;

.slick-slide {
  position: relative;
  &:focus {
    outline: none;
  }
}

.slick-initialized {
  height: auto;
  visibility: visible;
}

/* Custom arrows */
.slick-prev,
.slick-next {
  background: transparent;
  border: 0;
  color: transparent;
  cursor: pointer;
  display: block;
  font-size: 0;
  height: 20px;
  line-height: 0;
  outline: none;
  padding: 0;
  position: absolute;
  top: 60%;
  transform: translate(0, -50%);
  z-index: 1000;
  &:hover {
    &::before {
      color: #0a2c74;
    }
  }
}

.slick-prev {
  height: 100%;
  left: 0;
}

.slick-next {
  height: 100%;
  right: 0;
}

/* Custom arrow icon */
.slick-prev::before,
.slick-next::before {
  color: $color-grey;
  font-family: 'perfectfit';
  font-size: 70px;
}

.slick-next::before {
  content: '\e916';
}

.slick-prev::before {
  content: '\e917';
}

/* Dots */
.slick-dots {
  bottom: -25px;
  display: block;
  list-style: none;
  margin: 0;
  padding: 0;
  position: absolute;
  text-align: center;
  width: 100%;
  li {
    cursor: pointer;
    display: inline-block;
    height: 20px;
    margin: 0 5px;
    padding: 0;
    position: relative;
    width: 20px;
    button {
      background: transparent;
      border: 0;
      color: transparent;
      cursor: pointer;
      display: block;
      font-size: 0;
      height: 20px;
      line-height: 0;
      outline: none;
      padding: 5px;
      width: 20px;
      &:hover,
      &:focus {
        outline: none;
        &::before {
          opacity: $slick-opacity-on-hover;
        }
      }
      &::before {
        color: $slick-dot-color;
        content: $slick-dot-character;
        font-family: $slick-font-family;
        font-size: $slick-dot-size;
        -moz-osx-font-smoothing: grayscale;
        -webkit-font-smoothing: antialiased;
        height: 20px;
        left: 0;
        line-height: 20px;
        opacity: $slick-opacity-not-active;
        position: absolute;
        text-align: center;
        top: 0;
        width: 20px;
      }
    }
    &.slick-active button::before {
      color: $slick-dot-color-active;
      opacity: $slick-opacity-default;
    }
  }
}

.slick-dots li button::before {
  color: $color-grey;
}
