
[type='checkbox'],
[type='radio'] {
  + label {
    margin-left: 0;
  }
}

.form-item {
  &__info {
    font-size: 0.8em;
    padding-left: 5px;
  }
  select {
    background: url('../images/common/icons/select-arrow.svg') no-repeat 98% transparent;
    background-position: 95%;
    width: 100%;

    @include respond-to('desktop') {
      background-position: 98%;
    }
  }
}

$border-size: 3px;
$inner-size: 30px;
$outer-size: $inner-size + 6;
//#Checkbox
.form-item[type='checkbox']:not(:checked),
.form-item[type='radio']:not(:checked) {
  left: -9999px;
  position: absolute;
  + label {
    cursor: pointer;
    display: inline-block;
    line-height: $outer-size;
    margin-right: 10px;
    padding-left: $outer-size + 10px;
    position: relative;

    @include respond-to('desktop') {
      margin-right: 20px;
    }
    &::before {
      background: #fff;
      border: 1px solid $color-grey;
      content: '';
      height: $outer-size;
      left: 0;
      position: absolute;
      top: 0;
      width: $outer-size;
    }
    &::after {
      background: $form-color;
      content: '';
      height: $inner-size;
      left: $border-size;
      opacity: 0;
      position: absolute;
      top: $border-size;
      transform: scale(0);
      transition: all 0.2s ease;
      width: $inner-size;
    }
  }
}

.form-item[type='checkbox']:checked,
.form-item[type='radio']:checked {
  left: -9999px;
  position: absolute;
  + label {
    cursor: pointer;
    display: inline-block;
    line-height: $outer-size;
    margin-right: 10px;
    padding-left: $outer-size + 10px;
    position: relative;

    @include respond-to('desktop') {
      margin-right: 20px;
    }
    &::before {
      background: #fff;
      border: 1px solid $color-grey;
      content: '';
      height: $outer-size;
      left: 0;
      position: absolute;
      top: 0;
      width: $outer-size;
    }
    &::after {
      background: $form-color;
      content: '';
      height: $inner-size;
      left: $border-size;
      opacity: 1;
      position: absolute;
      top: $border-size;
      transform: scale(1);
      transition: all 0.2s ease;
      width: $inner-size;
    }
  }
}

.form-item[type='radio']:not(:checked) {
  + label {
    &::before {
      border-radius: 50%;
    }
    &::after {
      border-radius: 50%;
    }
  }
}

.form-item[type='radio']:checked {
  + label {
    &::before {
      border-radius: 50%;
    }
    &::after {
      border-radius: 50%;
    }
  }
}

.form-item[type='checkbox']:disabled,
.form-item[type='radio']:disabled {
  &:not(:checked) + label {
    cursor: not-allowed;
    &::before {
      background-color: #eee;
      border-color: #ccc;
    }
  }
  &:checked + label {
    cursor: not-allowed;
    &::before {
      background-color: #eee;
      border-color: #ccc;
    }
    &::after {
      background: #aaa;
    }
  }
}

.checkbox-container {
  margin: 1rem 0 1.5rem;
}

.error-message {
  color: #c2001d;
  display: block;
  margin: 0 0 1rem;
}

input[class='error'] {
  border: 2px solid $form-error-color;
  &:focus {
    border: 2px solid $form-error-color;
  }
}

label[class='error'] {
  color: $form-error-color;
}
