$max-width: 1280px;
$color-product: $color-blue;
$color-benefit-title: $color-grey-medium;
$product-container-height: 490px;
$product-container-background-color: $white;
$product-title-block-height: 70px;
.products-slider-container {
    background: transparent;
    height: 380px;
    margin-bottom: 25px;
    .nav-tabs {
        background: $white;
        background-image: url('../images/common/ajax-loader.gif');
        background-position: center;
        background-repeat: no-repeat;
        height: 380px;
    }
    &--initialised {
        height: auto;
        margin-bottom: 0;
        .nav-tabs {
            background-image: none;
            height: auto;
        }
        .product-slider {
            display: block;
        }
    }
}

.products-filter {
    background: $color-grey;
    display: none;
    text-align: center;
    @include respond-to('desktop') {
        align-items: center;
        height: 50px;
        justify-content: center;
    }
    &--category {
        background: $white;
        display: flex;
        justify-content: center;
        padding: 10px 0;
        .inline-list__link {
            color: $color-grey;
            font-weight: bold;
        }
        .inline-list__item {
            color: $color-grey;
        }
    }
}

.inline-list__item--subcategory {
    @include respond-to('all-phones') {
        border-bottom: 1px solid $color-grey-light;
        display: block;
        padding: 10px;
        &::after {
            content: '';
            padding: 0;
        }
    }
    @include respond-to('all-tablets') {
        border-bottom: 1px solid $color-grey-light;
        display: block;
        padding: 10px;
        &::after {
            content: '';
            padding: 0;
        }
    }
}

.products-header {
    text-align: center;
}

.product-slider__box {
    padding: 0 0 25px;
    text-align: center;
    .img--responsive {
        cursor: pointer;
        padding: 10px;
    }
}

.product-slider__box:focus {
    outline: none;
}

.triangle-down {
    background: $white;
    display: none;
}

.triangle-down--selected {
    display: block;
}

.triangle-down::after {
    border-left: 25px solid transparent;
    border-right: 25px solid transparent;
    border-top: 25px solid;
    border-top-color: $white;
    bottom: 0;
    content: '';
    left: calc(50% - 25px);
    position: absolute;
    transform: rotate(180deg);
    z-index: 1;
    @include respond-to('desktop') {
        transform: rotate(0);
    }
}

$triangle-color: ( violet: #B288B9,
green: #008755,
lightBlue: #489FDF,
darkBlue: #385E9D,
yellow: #EBBC4E,
pink: #CE0F69,
white: #FFF,
green2: #719949,
purple: #B288B9,
lighterBlue: #3CB3E8,
darkerBlue: #5F8DDA,
pinkRed: #E41466,
activepink:#ce0f69,
lightGreen: #10857E);
@each $i,
$color in $triangle-color {
    [data-color='#{$color}'] .triangle-down::after {
        border-top-color: #{$color};
    }
}

@each $i,
$color in $triangle-color {
    .product-slider__item[data-color='#{$color}'] {
        background: $white;
        @include respond-to('desktop') {
            background: #{$color};
        }
    }
}

.product-information {
    overflow: hidden;
    display: none;
    &--initialised {
        display: block;
    }
}

.products-header-title {
    background: $background-color;
    border: 0;
    margin: 0;
    text-align: center;
}

.nav-tabs__item {
    display: none;
}

.product-list {
    display: none;
    margin: 0;
    padding: 0;
}

.product-list__item {
    display: block;
}

.product-info-container {
    background: $product-container-background-color;
    margin: 0;
    @include respond-to('desktop') {
        height: $product-container-height;
        overflow: hidden;
    }
}

.product-info {
    position: relative;
    @include respond-to('desktop') {
        padding: 0 5px;
    }
}

.product-info__title {
    align-items: center;
    background-color: $color-product;
    color: $white;
    display: flex;
    font-size: 23px;
    height: $product-title-block-height;
    justify-content: center;
    text-align: center;
    @include respond-to('desktop') {
        font-size: 22px;
    }
}

.product-benefit {
    display: flex;
    height: auto;
    margin: 5px 0;
    @include respond-to('desktop') {
        margin: 3px 0;
    }
}

.product-benefit__text {
    align-content: right;
    align-items: center;
    background: $color-grey-light;
    color: $black;
    display: flex;
    flex-direction: row-reverse;
    font-size: 14px;
    font-weight: 700;
    margin-left: auto;
    padding-right: 5px;
    text-align: right;
    width: 100%;
}

.product-benefit__box {
    background: $color-product;
    width: 60px;
}

.product-info__packshot {
    display: none;
    @include respond-to('desktop') {
        align-items: center;
        cursor: pointer;
        display: flex;
        //height: 260px;
        justify-content: center;
        position: relative;
        button,
        picture {
            cursor: inherit;
        }
    }
}

.product-description {
    background: $color-product;
    @include respond-to('desktop') {
        height: $product-container-height;
        overflow: auto;
        &::-webkit-scrollbar {
            height: 7px;
            width: 7px;
        }
        &::-webkit-scrollbar-button {
            height: 0;
            width: 0;
        }
        &::-webkit-scrollbar-thumb {
            background: #e1e1e1;
            border: 0 none #fff;
            border-radius: 50px;
        }
        &::-webkit-scrollbar-thumb:hover {
            background: #fff;
        }
        &::-webkit-scrollbar-thumb:active {
            background: #bcbcbc;
        }
        &::-webkit-scrollbar-track {
            background: #666;
            border: 0 none #fff;
            border-radius: 50px;
        }
        &::-webkit-scrollbar-track:hover {
            background: #666;
        }
        &::-webkit-scrollbar-track:active {
            background: #535353;
        }
        &::-webkit-scrollbar-corner {
            background: transparent;
        }
    }
    p {
        color: $white;
    }
}

$lang-with-bullet: ( 0: 'de', 1: 'nl');
@each $index, $lg in $lang-with-bullet {
    :lang(#{$lg}) > &.bullet::before {
        content: '• ';
    }
}
.product-description__text {
    padding: 20px 35px 30px;
    text-align: center;
    
    @include respond-to('desktop') {
        padding: 15px 25px 40px 35px;
        text-align: left;
    }
}

.product-description__flavours {
    display: none;
    overflow: hidden;
    @include respond-to('desktop') {
        display: flex;
        height: $product-title-block-height;
        justify-content: center;
        padding-top: 10px;
    }
}

.product-lifestyle {
    display: none;
    @include respond-to('desktop') {
        background-size: cover;
        display: block;
        height: 490px;
        width: 100%;
    }
}

.product-list__item {
    display: none;
}

.product-zoom--button {
    cursor: pointer;
    &:focus {
        outline: 0;
    }
}

.product-zoom {
    bottom: 0%;
    left: 45%;
    position: absolute;
}

.selected {
    display: block;
    @include respond-to('desktop') {
        display: flex;
    }
}

.nav-tabs {
    margin: 0;
    padding: 0;
}

.product-slider__item {
    background: $white;
    padding: 0 0 5px;
    p {
        color: $white;
    }
}

.product-slider {
    background-color: $product-container-background-color;
    margin-left: auto;
    margin-right: auto;
    max-width: initial;
    display: none;
    width: 100%;
}

.slick-initialized {
    visibility: visible;
}

.slick-list {
    @include respond-to('desktop') {
        margin: 0 auto;
        width: 100%;
    }
}

.slick--centered {
  .slick-list {
    @include respond-to('desktop') {
      display: flex;
      justify-content: center;
    }
  }
}