$cat-color: #3e61ae;
$dog-color: #c31470;
.pet-types {
  &__container {
    display: flex;
  }
}

.pet-types__container {
  label {
    flex-grow: 1;
    margin: 0;
    &:nth-of-type(even) {
      margin-left: 10px;
    }
    &:nth-of-type(odd) {
      margin-right: 10px;
    }
  }
  .pet-type__thumbnail {
    opacity: 0.6;
    &:hover {
      cursor: pointer;
      opacity: 1;
    }
  }
  .pet-type__thumbnail--selected {
    opacity: 1;
  }
  .pet-type__thumbnail--unselected {
    background-color: #dadada;
    opacity: 0.6;
  }
}

[id^='registration-pet-tab-content-cat'],
[id^='registration-pet-tab-content-dog'] {
  display: none;
}

[id^='registration-pet-tab-cat']:checked~[id^='registration-pet-tab-content-cat'],
[id^='registration-pet-tab-dog']:checked~[id^='registration-pet-tab-content-dog'] {
  display: block;
}

.pet-type {
  &__wrapper {
    flex-grow: 1;
  }
  &__thumbnail {
    background-color: #3e61ae;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    height: 115px;
    justify-content: center;
    margin: 0 auto;
    padding: 0;
    position: relative;
    width: 100%;
    @include respond-to('desktop') {
      height: 300px;
    }
    @include respond-to('portrait-tablets') {
      height: 150px;
    }
    @include respond-to('tablets') {
      height: 250px;
    }
    picture {
      display: flex;
    }
    &--cat {
      background-color: $cat-color;
      background-image: url('../images/common/registration/registration-cat.jpg');
      background-position: 50% 15%;
      @include respond-to('desktop') {
        background-position: 50% 15%;
      }
      @include respond-to('portrait-tablets') {
        background-position: 50% 15%;
      }
      @include respond-to('tablets') {
        background-position: 50% 15%;
      }
    }
    &--dog {
      background-color: $dog-color;
      background-position: 100%;
      background-image: url('../images/common/registration/registration-dog.jpg');
      @include respond-to('desktop') {
        background-position: 100%;
      }
      @include respond-to('portrait-tablets') {
        background-position: 50% 15%;
      }
      @include respond-to('tablets') {
        background-position: 50% 15%;
      }
    }
    img {
      width: auto;
      height: 100%;
    }
    input {
      display: none;
    }
  }
  &__placeholder {
    background-image: none;
    height: 115px;    
    @include respond-to('portrait-tablets') {
      height: 230px;
    }
    @include respond-to('tablets') {
      height: 230px;
    }

    @include respond-to('desktop') {
      height: 300px;
    }
    &--preview {
      background-image: none !important;
    }
  }

  &__caption {
    display: inline-block;
    font-size: 18px;
    margin: 10px 0 20px;
    text-align: center;
    text-transform: uppercase;
    width: 50%;
    @include respond-to('desktop') {
      font-size: 36px;
      margin: 20px 0 40px;
    }
  }
  &__icon {
    cursor: pointer;
    position: absolute;
    right: 50px;
    bottom: -13%;
    @include respond-to('desktop') {
      bottom: -10%;
    }
    @include respond-to('portrait-tablets') {
      bottom: -5%;
    }
    @include respond-to('tablets') {
      bottom: -6%;
    }
    i {
      font-size: 15px;
      @include respond-to('desktop') {
        font-size: 30px;
      }
    }
    &--delete-picture {
      right: 100px;
      @include respond-to('desktop') {
        right: 150px;
      }
    }
  }
  &__max-size-msg {
    color: #c2001d;
    display: none;
  }
}

.pet-type__placeholder[data-category='dog'] {
  background-image: url(../images/common/registration/registration-dog.jpg);
  background-size: cover;
  background-position: 0% 25%;
  @include respond-to('desktop') {
    background-position: 0% 20%;
  }
  @include respond-to('portrait-tablets') {
    background-position: 0% 40%;
  }
  @include respond-to('tablets') {
    background-position: 0% 20%;
  }
}

.pet-type__placeholder[data-category='cat'] {
  background-image: url(../images/common/registration/registration-cat.jpg);
  background-size: cover;
  background-position: 0 0;
  @include respond-to('desktop') {
    background-position: 0 0;
  }
  @include respond-to('portrait-tablets') {
    background-position: 0 0;
  }
  @include respond-to('tablets') {
    background-position: 0 5%;
  }
}
