.profile__heading {
  font-family: $font-paragraph;
  font-weight: normal;
  margin: 0;
  padding: 10px 0 0;
  text-align: center;

  @include respond-to('desktop') {
    padding: 20px 0 0;
  }
  span {
    display: block;
    font-family: $font-title-bold;
  }
}

.profile__icon {
  color: $color-blue-darkest;
  font-size: 40px;
  padding-bottom: 10px;
  text-align: center;

}

.profile__save-btn {
  i {
    display: none;
    float: right;
    font-size: 13px;
  }
  i.visible {
    display: inline-block;
  }
}

.profile--editable {
  .profile__summary {
    display: none;
  }
  .profile__form {
    display: block;
  }
  // Pet button actions
  .picture-add,
  .picture-delete {
    display: flex;
  }
  .profile__picture {
    display: none;
  }
  .pet-form-group {
    @include respond-to('desktop'){
      margin-top: 20px;
      padding: 0 25px;
    }
  }
  .pet-type__captions,
  .pet-types__container,
  .label--pet-choice {
    display: none;
  }
}

.pet-form-group {
  margin-top: 20px;
}

.profile__summary {
  padding: 10px;

  @include respond-to('desktop') {
    padding: 50px;
  }
  p {
    margin: 0;
  }
}

.profile__form {
  display: none;
}

.profile__picture {
  position: relative;
}

@mixin profile-picture-button($position-mobile, $position-desktop) {
  bottom: -15px;
  display: none;
  position: absolute;
  right: $position-mobile;

  @include respond-to('desktop') {
    bottom: -25px;
    right: $position-desktop;
  }

  @include respond-to('all-tablets') {
    bottom: -15px;
    right: $position-desktop;
  }
}

.picture-delete {
  @include profile-picture-button(35%, 150px);
}

.picture-add {
  @include profile-picture-button(20%, 20%);
}

.remove-pet {
  padding: 30px 0;
  text-align: center;
  &__button {
    background: $color-grey-light;
    border: 2px $white solid;
    font-size: 50px;
    height: 100%;
    margin: 0 auto;
    width: 60px;
  }
  &__text {
    color: $color-grey;
  }
}

.popin-deactivate-account-confirmation,
.popin-confirmation {
  text-align: center;
  &__buttons {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
  }
}
