.popin-deactivate-account-confirmation,
.total-five-benefits-popin,
.popin-confirmation,
.resent-email,
.login-popin {
  background: $white;
  margin: 20px auto;
  max-width: 500px;
  padding: 20px;
  position: relative;
  &__header {
    text-align: center;
    padding-left: 10px;
    padding-right: 10px;
    .mandatory-info {
      padding-top: 20px;
    }
  }

}
  .message {
    color: $form-error-color;
    display: none;
    padding-bottom: 20px;
    text-align: center;
    &--visible {
      display: block;
    }
    &--success {
      color: $form-success-color;
    }
  }



  .loader {
    display: flex;
    justify-content: center;
    padding-bottom: 10px;
    img {
      display: none;
    }
  }

.mfp-content {
  h2 {
    margin: 0;
  }
  .overlapping-section__introduction {
    @include respond-to('desktop') {
      max-width: 460px;
    }
  }
}

.total-five-benefits-popin {
  max-width: 800px;
  h4 {
    margin-bottom: 0;
    margin-left: 20px;
    margin-right: 20px;
    margin-top: 0;
  }
  p {
    margin-left: 20px;
    margin-right: 20px;
  }
  img {
    margin-top: 0;
    margin-bottom: 0;
  }
}
