.select-date {
  &__wrapper {
    display: flex;
    select {
      background: url('../images/common/icons/select-arrow.svg') no-repeat transparent;
      background-position: 95%;
      flex-grow: 1;
      text-align: center;
      text-align-last: center;

      @include respond-to('desktop') {
        background-position: 98%;
      }
    }
    select:nth-child(2) {
      margin: 0 10px;

      @include respond-to('desktop') {
        margin: 0 20px;
      }
    }
  }
}
