.registration-pet {
  p.label {
    font-size: 16px;
    margin: 10px 0 0;

    @include respond-to('desktop') {
      margin: 2px 0 0;
    }
  }
  .collapsible-box {
    margin: 15px;

    @include respond-to('desktop') {
      margin: 30px;
    }
    h2 {
      margin: 0;
    }
    .collapsible-box__icon {
      display: block;
    }
    .close-wrapper {
      i {
        font-size: 20px;

        @include respond-to('desktop') {
          font-size: 40px;
        }
      }
    }
  }
  &__terms {
    padding: 0 15px;

    @include respond-to('desktop') {
      padding: 0 30px;
    }
  }
  &__condition-checkbox {
    padding: 30px 0 25px;

    @include respond-to('desktop') {
      padding: 60px 0 55px;
    }
  }
  &__condition {
    padding: 0 0 25px;

    @include respond-to('desktop') {
      padding: 0 0 50px;
    }
  }
  &__register-btn {
    padding-bottom: 35px;

    i {
      display: none;
      float: right;
      font-size: 13px;
    }
    i.visible {
      display: inline-block;
    }

    @include respond-to('desktop') {
      padding-bottom: 70px;
    }
  }
  &__max-pets {
    display: none;
  }
  &__remove-pet {
    background: #c1c1c1;
    border: none;
    bottom: 20px;
    display: none;
    position: absolute;
    right: 20px;
  }
  &__add-another-pet {
    display: none;
  }
  &__validation {
    display: none;
  }
}
.condition_item {
  padding-bottom: 5px;
  // label { 
  //   line-height: 20px !important;
  //   margin-bottom: 10px;
  //   margin-top: 10px;
  // }
  @include respond-to('desktop') {
    padding-bottom: 10px;
  }
}

.condition_item.parsley-error {
  border: 0;
  label {
    color: $form-error-color;
  }
}

#profile-birthdate,
.js-add-another-pet {
  cursor: pointer;
}
.js-add-another-pet-profile {
  @extend .js-add-another-pet;
}


