.banner {
  position: relative;

  @include respond-to('desktop') {
    margin-bottom: -3px;
  }
}

.banner__mbg {
  align-items: center;
  background-color: #b4bbb3;
  cursor: pointer;
  display: flex;
  height: 15%;
  min-height: 38px;
  min-width: 122px;
  // padding: 0 10px;
  position: absolute;
  right: 0;
  width: 37%;

  @include respond-to('desktop') {
    height: 80px;
    // padding: 0 20px;
    width: 245px;
  }
  .banner__promo {
    display: flex;
    align-items: center;
    justify-content: center;

    color: $color-grey;
    font-family: $font-title-bold;
    font-size: 2.5vw;
    line-height: 1.1;
    text-decoration: none;
    text-transform: uppercase;
    word-break: break-word;
    @include respond-to('all-tablets') {
      font-size: 3vw;
    }

    @include respond-to('desktop') {
      font-size: 14px;
    }
    span {
      display: inherit;
      padding: 0 0 0 5%;
      width: 100%;
      @include respond-to('desktop') {
        font-size: 14px;
      }
    }
  }
  i[class^=icon-] {
    color: #f7901c;
    font-size: 30px;
    font-size: 4vw;
    padding: 0 5%;
    position: relative;
    text-align: center;

    @include respond-to('all-tablets') {
      font-size: 5vw;
      padding: 0 7%;
    }
    @include respond-to('desktop') {
      font-size: 35px;
      padding: 0 8%;
    }
  }
}

.banner__title {
  color: $white;
  font-family: $font-title-bold;
  margin: 10px;
}

.banner__text {
  color: $white;
  margin: 0;
}

.banner__info {
  background: $color-green;
  bottom: 25px;
  color: $white;
  padding: 10px 45px 10px 10px;
  position: absolute;
  right: 0;

  @include respond-to('desktop') {
    bottom: 3px;
  }
  &--home {
    background-color: $color-blue;
    bottom: 25px;
    padding: 10px 15px 10px 10px;
    position: absolute;
    right: 0;
    text-transform: uppercase;
    width: 70%;

    @include respond-to('desktop') {
      bottom: 309px;
      height: auto;
      padding: 10px 30px;
      width: 525px;
    }

    @include respond-to('all-tablets') {
      padding: 10px 20px;
    }
    .banner__title {
      color: $white;
      font-size: 12px;
      line-height: 1.3;

      @include respond-to('desktop') {
        font-size: 40px;
      }

      @include respond-to('all-tablets') {
        font-size: 30px;
      }
    }
    span {
      font-family: $font-paragraph;
      font-weight: normal;
      &::before {
        content: ' ';
      }
    }
  }
}
:lang(fr),
:lang(de),
:lang(nl) {
  .banner__title {
    @include respond-to('desktop') {
      font-size: 30px;
    }
  }
}
.overview-container {
  @include respond-to('desktop') {
    padding-bottom: 25px;
  }
}

.coupon-confirmation {
  .overview-box {
    @include respond-to('desktop') {
      margin: 0 250px -230px;
      min-height: 250px;
      position: relative;
      top: -260px;
    }
  }
  .section-content {
    @include respond-to('desktop') {
      padding-bottom: 20px;
    }
  }
}

.overview-box {
  background: $white;
  margin: 0;
  padding: 10px;
  position: relative;
  text-align: center;
  top: -10px;
  //word-break: break-all;
  // :lang(nl) {
  //   span {
  //     font-size: 25px;
  //   }
  //   @include respond-to('desktop') {
  //     span {
  //       font-size: 30px;
  //     }
  //   }
  // }
    
  &__subtitle {
    display: block;
    font-family: $font-title-bold;
    font-weight: normal;
  }
  &__title--simple {
    font-family: $font-title-paragraph;
    font-weight: 400;
  }

  @include respond-to('desktop') {
    margin: 0 250px -250px;
    min-height: 250px;
    position: relative;
    top: -248px;
  }
  &--product-detail {
    margin: 0;
    min-height: 0;
    position: static;
  }
  &__paragraph {
    font-size: 14px;
    padding: 0 20px;
    text-align: center;

    @include respond-to('desktop') {
      font-size: 22px;
      padding: 0 50px;
    }
    @include respond-to('all-tablets') {
    font-size: 22px;
    }
  }
}
