.thumbnail-box {
  margin: 0 auto;
  max-width: 480px;
  padding: 25px 20px;

  @include respond-to('desktop') {
    padding: 25px;
  }
  &__icon {
    align-items: center;
    display: flex;
    height: 195px;
    margin: 0 auto;
    padding: 0;
    width: 195px;

    @include respond-to('desktop') {
      height: 220px;
      width: 220px;
    }
    img {
      width: 100%;
    }
  }
  &__caption {
    max-width: 185px;
    margin: 25px auto;
    word-wrap: break-word;
  }
  &__content {
    text-align: center;
    &-collapsed {
      margin-bottom: 5px;
      text-align: left;
    }
  }
  &__content-collapsed {
    margin: 0;
  }
  &__more {
    margin: 0 auto;
    text-align: center;
    width: 140px;
  }
  &__less {
    display: block;
    margin: 0 auto;
    width: 35px;
  }
}
