.box-info {
  align-items: center;
  display: flex;
  flex-flow: column wrap;
  height: 100px;
  justify-content: center;
  position: relative;
  width: 138px;
  &__text {
    color: $white;
    font-size: 16px;
  }
  &__text--quantity {
    font-size: 40px;
    font-weight: bold;
    position: relative;
    .power {
      font-size: 24px;
      font-weight: bold;
      position: absolute;
      top: 0;
    }
  }
  &__text--unit {
    font-size: 14px;
  }
  &--wrapper {
    display: flex;
    flex-flow: row nowrap;
  }
  &--plus {
    &::after {
      color: $white;
      content: '+';
      font-weight: bold;
      position: absolute;
      right: 0;
      top: 45%;
    }
  }
  &--linker {
    font-size: 16px;
    font-weight: bold;
    padding-left: 65px;

  }
}


