.select-wrapper {
  background: $white;
  display: inline-block;
  position: relative;
  vertical-align: middle;
  width: 100%;
  z-index: 3;
  &::after {
    content: '\e911';
    font-family: perfectfit;
    font-size: 36px;
    position: absolute;
    right: 0;
    top: 0;
    z-index: -1;
  }
  &--color {
    color: #95a5a6;
  }
}

.select-box {
  appearance: none;
  background: transparent;
  color: #95a5a6;
  cursor: pointer;
  margin-bottom: 0;
  outline: none;
  padding: 9px 5px 9px 10px;
  width: 100%;
  &:focus {
    background: transparent;
  }
}

select::-ms-expand {
  display: none;
}
