
.table {
  border-collapse: collapse;
  width: 100%;
  &__row {
    border: inset;
    border-width: 1px 0;
    &:first-child {
      border-top: 0;
    }
  }
  &__cell {
    font-family: $font-paragraph;
    padding: 10px 0;
    &:last-child {
      text-align: right;
    }
  }
}
