$height: 60px;
$height-detail: 85px;
$space-between: 10px;

.product-box {
  margin: 0 0 100px;
  &__header {
    height: $height;
  }
  &__title {
    align-items: center;
    background: #de065d;
    color: $white;
    display: flex;
    font-family: $font-title-paragraph;
    font-size: 18px;
    height: $height;
    justify-content: flex-end;
    padding: 0 15px;

    @include respond-to('desktop') {
      font-size: 33px;
    }

    @include respond-to('all-tablets') {
      font-size: 23px;
    }
    &--detail {
      height: $height-detail;
      justify-content: flex-start;

      @include respond-to('desktop') {
        justify-content: flex-end;
      }
    }
  }
  &__icon {
    align-items: center;
    background: #ed6e82;
    display: flex;
    height: $height;
    justify-content: flex-start;
    padding-left: 15px;
    &--detail {
      height: $height-detail;
    }
  }
  &__img {
    left: 20px;
    margin-top: -100px;
    position: relative;
  }
  &__description {
    padding-left: 30px;
    .btn {
      margin-top: 10px;

      @include respond-to('desktop') {
        margin-top: 20px;
      }
    }

    @include respond-to('desktop') {
      padding-left: 70px;
    }
  }
  &--detail {
    margin: 0;
    
    @include respond-to('desktop') {
      padding-top: 100px;
    }
  }
}

.product-box__wrapper:nth-of-type(odd) .product-box {
  @include respond-to('desktop') {
    padding-right: $space-between;
  }
}

.product-box__wrapper:nth-of-type(even) .product-box {
  @include respond-to('desktop') {
    padding-left: $space-between;
  }
}
