$navigation-height: 64px;
$navigation-height-mobile: 50px;
.header {
  background-color: $white;
  height: $navigation-height-mobile;
  position: relative;
  @include respond-to('desktop') {
    background-color: $header-background-color;
    height: $navigation-height;
  }
}

.navbar-wrapper {
  display: flex;
  position: relative;
}

.navbar {
  display: none;
  @include respond-to('desktop') {
    background: $color-grey;
    display: block;
    height: $navigation-height;
    padding: 0 25px;
    width: 75%;
  }
  .inline-list__link {
    font-family: $font-standart-condensed;
  }
  .inline-list__item {
    font-size: 20px;
  }

}

.navbar__left {
  @include respond-to('desktop') {
    align-items: center;
    display: flex;
    float: left;
    height: 100%;
  }
}

.navbar__right {
  display: none;
  @include respond-to('desktop') {
    align-items: center;
    display: flex;
    flex-direction: row-reverse;
    height: 100%;
    text-align: right;
  }
}

.header__logo {
  background-color: $white;
  img {
    align-items: center;
    display: flex;
    padding: 10px 100px;
  }
  @include respond-to('desktop') {
    float: left;
    height: 64px;
    width: 25%;
    img {
      padding: 15px 52px;
    }
  }
}

.header-confirmation {
  text-align: center;
  img {
    padding: 15px 60px;
    width: 250px;
    @include respond-to('desktop') {
      width: 350px;
    }
  }
}

.nav {
  background: $white;
  display: flex;
  select {
    background: $white;
    margin: 0;
    text-align: center;
    text-align-last: center;
    width: 100%;
  }
  @include respond-to('desktop') {
    align-items: center;
    display: flex;
    height: 55px;
    justify-content: center;
    select {
      display: none;
    }
  }
  &--subcategory {
    background: $color-grey;
    display: none;
    &-open {
      display: none;
      @include respond-to('desktop') {
        display: flex;
      }
    }
  }
  &--open {
    display: none;
    @include respond-to('desktop') {
      display: flex;
    }
  }
}

.btn--menu-toggle {
  align-items: center;
  background: transparent;
  border: 0;
  color: $color-grey;
  display: flex;
  font-size: 25px;
  height: 100%;
  margin-top: 0;
  padding: 0 15px;
  position: absolute;
  &:hover {
    border: 0;
    outline: none;
    padding: 0 15px;
  }
  @include respond-to('desktop') {
    display: none;
  }
  @include respond-to('all-tablets') {
    font-size: 50px;
  }
}

.nav-mobile {
  background: $white;
  display: none;
  position: absolute;
  width: 100%;
  z-index: 1;
  ul {
    line-height: 20px;
    list-style: none;
    margin: 0;
    padding: 10px 0 10px 20px;
    text-transform: uppercase;
  }
  a {
    color: $color-grey;
    font-family: $font-standart-condensed;
    text-decoration: none;
  }
  @include respond-to('desktop') {
    display: none !important;
  }
  .img--perfectfit-plus {
    height: 12px;
    width: auto;
  }
}

.navbar--perfect-plus {
  font-size: 12px;
  .inline-list__item {
    font-size: 18px;
  }
}

.img--perfectfit-plus {
  height: 18px;
  width: auto;
}

.img--perfectfit-plus+i {
  color: $color-orange;
}

.inline-list__item--perfectfit-plus {
  font-size: 18px;
}

// specific rules for fr and nl
:lang(fr),
:lang(nl) {
  .inline-list__item{
    font-size: 15px;
  }
  .inline-list__item--perfectfit-plus {
    font-size: 15px;
  }
  .img--perfectfit-plus {
    height: 13px;
  }
}
