.inline-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  &--nav {
    display: none;

    @include respond-to('desktop') {
      display: block;
    }
  }
  &--flex {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }
}

.inline-list__item--perfectfit-plus {
  color: $white;
  display: inline-flex;
  &::after {
    content: '';
    padding: 0 2px;
    @include respond-to('desktop') {
      padding: 0 5px;
    }
  }
}

.inline-list__item {
  color: $white;
  display: inline-flex;
  &::after {
    content: '';
    padding: 0 10px;
    @include respond-to('desktop') {
      padding: 0 20px;
    }
  }
  &:last-of-type::after {
    content: '';
    padding: 0;
  }
  &--piped {
    &::after {
      content: '|';
      padding: 0 10px;
    }
    &:last-of-type::after {
      content: '';
      padding: 0;
    }
  }
  &--slashed {
    &::after {
      content: '/';
      padding: 0 10px;
    }
    &:last-of-type::after {
      content: '';
      padding: 0;
    }
  }
  &--dark {
    color: $color-grey;
  }
}

.inline-list__link {
  color: $white;
  font-family: $font-standart-condensed;
  position: relative;
  text-decoration: none;
  text-transform: uppercase;
  i[class^='icon-'] {
    margin-right: 5px;
  }
  &:hover,
  &:active,
  &:focus,
  &--active,
  &--selected {
    text-shadow: 0 0 1px;
  }
  &--dark {
    color: $color-grey;
  }
  &::before {
    bottom: -15px;
    content: '';
    left: -12px;
    position: absolute;
    right: -12px;
    top: -15px;
    z-index: 40;
  }
}
