$form-error-color: #c2001d;
.parsley-error {
  border: 2px solid $form-error-color;
}

.parsley-errors-list {
  color: $form-error-color;
  list-style-type: none;
  margin: 10px 0;
  padding-left: 0;
  transition: all 0.3s ease-in;
}

.parsley-errors-list.filled {
  color: $form-error-color;
  opacity: 1;
}
.radio-button-container {
  border: 0;
  label {
    margin: 0 0 10px;
  }
}
.radio-button-container.parsley-error {
  border: 0;
  label {
    color: $form-error-color;
  }
}
.checkbox-validation-container {
  border: 0;
  label {
    margin: 0 0 10px;
  }
}
.checkbox-validation-container.parsley-error {
  border: 0;
  label {
    color: $form-error-color;
  }
}

.select-date-error {
  select:nth-child(2), select:nth-child(3){
    border: 2px solid $form-error-color;
  }
}

.select-date__error-message {
  color: $form-error-color;
  display: none;
  margin: 10px 0;
}