
html {
  font-family: $default-font-family;
  font-size: 100%;
  line-height: 1;
  min-height: 100%;
  overflow-y: scroll;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

::selection {
  background-color: $color-grey-light;
}

.visibility--hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

body {
  color: $color-grey;
}

