h1 {
  color: $color-grey;
  font-family: $font-title-bold;
  font-size: 28px;
  text-transform: uppercase;
  @include respond-to('desktop') {
    font-size: 48px;
  }
  @include respond-to('all-tablets') {
    font-size: 48px;
  }
}

h2 {
  color: $color-grey;
  font-family: $font-title-bold;
  font-size: 25px;
  text-transform: uppercase;
  // word-break: break-all;
  @include respond-to('desktop') {
    font-size: 40px;
  }
  @include respond-to('all-tablets') {
    font-size: 40px;
  }
}

h3,
h4 {
  color: $color-grey;
  font-family: $font-title-bold;
  font-size: 17px;
  text-transform: uppercase;
  @include respond-to('desktop') {
    font-size: 27px;
  }
  @include respond-to('all-tablets') {
    font-size: 27px;
  }
}

p {
  color: $color-grey;
  font-family: $font-paragraph;
  font-size: 13px;
  line-height: 1.6;
  @include respond-to('desktop') {
    font-size: 16px;
  }
  @include respond-to('all-tablets') {
    font-size: 22px;
  }
}

.introduction {
  font-weight: bold;
}

.section-heading {
  margin: 0;
  padding: 10px 0;
  text-align: center;
  @include respond-to('desktop') {
    padding: 20px 0;
  }
  span {
    display: block;
    font-family: $font-paragraph;
    font-size: 25px;
    line-height: 1.5;
    @include respond-to('desktop') {
      font-size: 30px;
      line-height: 1;
    }
  }
  &--spaced {
    padding: 20px 0;
    @include respond-to('desktop') {
      padding: 35px 0;
    }
  }
  &__icon {
    color: $color-blue;
    font-size: 40px;
    text-align: center;
  }
  &__title {
    margin: 0;
  }
}

.bold {
  font-weight: bold;
}

.title {
  font-family: $font-title-paragraph;
  font-size: 24px;
  text-transform: uppercase;
  margin: 15px 0;
  line-height: 1;
  @include respond-to('desktop') {
    margin: 20px 0;
    font-size: 40px;
  }
  @include respond-to('all-tablets') {
    font-size: 32px;
  }
  .perfectfit-plus-title {
    img {
      height: 25px;
      @include respond-to('desktop') {
         height: 40px;
      }
      @include respond-to('all-tablets') {
        height: 34px;
      }
    }
  }
}
