#mars-footer-panel.custom-mars-footer {
  font-family: $font-standart-condensed;
  padding-bottom: 10px !important;
  padding-top: 10px;
  .mars-footer-list-right span {
    font-size: 11px!important;

    @include respond-to('desktop') {
      font-size: 15px!important;
    }
  }
  .mars-footer-legal {
    padding: 10px;
    p {
      color: $white;
      font-family: $font-standart-condensed;
      font-size: 12px;
      text-align: center;
      @include respond-to('desktop') {
        font-size: 15px;
      }
    }
  }
  .mars-footer-label {
    font-family: $font-standart-condensed;
    text-transform: uppercase;
  }

}

.footer {
  &__logo {
    background: $color-grey-darkest;
    display: flex;
    justify-content: center;
    padding-bottom: 40px;
  }
  &__container {
    background: #65646d;
    margin-right: auto;
    margin-left: auto;
    position: relative;
    top: -11px;
    a,p{
      color: white;
      font-family: $font-standart-condensed;
      font-size: 13px;
      margin: 0;
    }
    a{
      text-decoration: underline;
    }
  }
  &--row {
    background: #65646d;
    display: none;
    height: 105px;
    margin-right: auto;
    margin-left: auto;
    position: absolute;
    top: -20px;
    width: 100%;
    @include respond-to('desktop') {
      top: -40px;
    }
  }
  &--image {
    text-align: right;
  }
  &--text {
    font-family: $font-standart-condensed;
    text-align: left;
    word-break: break-word;
  }
}
