// autocomplete styles
.autocomplete-suggestions {
  background: $white;
  line-height: 1.3;
  border: 1px solid;
  padding: 5px 10px;
}

.breed__carousel {
  visibility: hidden;
  .slick-next {
    right: -35px;
  }
  .slick-prev {
    left: -35px;
  }
}

.breed__search {
  font-weight: bold;
  &::-ms-clear {
    display: none;
  }
}

.breed {
  margin-bottom: 10px;
  &__filter {
    padding: 10px 0;
    @include respond-to('desktop') {
      padding: 20px 0;
    }
  }
}

.breed__item {
  padding-right: 10px;
  div,
  img,
  span {
    cursor: pointer;
  }
  &--selected {
    img {
      border: 3px solid $color-blue-dark;
    }
  }
}

.breed-item-wrapper {
  height: auto;
  position: relative;
  width: 100%;
  div {
    align-items: center;
    color: $white;
    display: flex;
    font-size: 13px;
    height: calc(100% - 8px);
    justify-content: center;
    left: 4px;
    position: absolute;
    text-transform: uppercase;
    top: 4px;
    width: calc(100% - 8px);
    @include respond-to('desktop') {
      font-size: 25px;
    }
    @include respond-to('all-tablets') {
      font-size: 25px;
    }
  }
  span {
    display: block;
    padding-bottom: 15px;
    padding-top: 5px;
    font-size: 13px;
    @include respond-to('desktop') {
      font-size: 16px;
    }
    @include respond-to('all-tablets') {
      font-size: 16px;
    }
  }
}

.breed__carousel {
  .slick-disabled {
    opacity: 0;
    pointer-events: none;
  }
}
