.mbg {
  &__form {
    padding: 0 10px;

    @include respond-to('desktop') {
      padding: 0 150px;
    }
  }
  &__title {
    font-size: 16px;

    @include respond-to('desktop') {
      font-size: 26px;
    }
  }
  &__info {
    padding: 20px 0;
    p {
      font-size: 13px;
    }
  }
  &__success {
    padding: 0 0 25px;
    @include respond-to('desktop') {
      padding: 0 0 45px;
    }
    p {
      font-size: 26px;
      line-height: 1.2;
      margin-top: 0;
      span {
        display: block;
        font-weight: 700;
        text-transform: uppercase;
      }
    }
  }
  &__img-preview {
    padding: 10px 2px 0;
    width: 185px;

    @include respond-to('desktop') {
      width: 375px;
    }
    .error-message {
      display: none;
    }
  }
  &__upload {
    display: none;
  }
  &__intro {
    width: auto;
    padding: 0;
    @include respond-to('desktop') {
      padding: 0 75px;
    }
  }
}
