$max-width: 1280px;

.page-container {
  margin: 0 auto;
  max-width: $max-width;
  position: relative;
}

.section-container {
  background: $white;
  margin: -1px 10px;
  position: relative;

  @include respond-to('desktop') {
    margin: -1px 50px;
  }
  &--spaced {
    margin: 20px 10px;

    @include respond-to('desktop') {
      margin: 25px 50px;
    }
  }
  &--transparent {
    background: transparent;
  }
}

.section-content {
  padding: 0 10px 25px;

  @include respond-to('desktop') {
    padding: 0 25px 50px;
  }
}

.no-padding {
  padding-left: 0;
  padding-right: 0;
}
