// Base
$btn-primary-color: $white;
$btn-font-size: 15px;
$btn-border-size: 3px;
$btn-background-color: #0a2c74;

// Fonts
$btn-font-primary: 'ITCAvantGardeStd-Demi';
$btn-font-hover: $sans-font-family;

// Passive btn
$btn-passive-opacity: 0.5;

.btn {
  background-color: $btn-background-color;
  border: 3px solid transparent;
  color: $btn-primary-color;
  cursor: pointer;
  display: inline-block;
  font-family: $btn-font-primary;
  font-size: 10px;
  height: 40px;
  margin: 2px;
  outline: none;
  padding: 5px 15px;
  text-decoration: none;
  text-transform: uppercase;
  vertical-align: middle;
  &:hover {
    @include respond-to('desktop') {
      background-color: $white;
      border: 3px solid $btn-background-color;
      color: $btn-background-color;
      // TO-DO Les composants bouge lorsqu'on change de font
      // font-family: $btn-font-hover;
      padding: 8px 30px;
    }
  }
  &.disabled {
    color: #fff;
    cursor: not-allowed;
    opacity: $btn-passive-opacity;
    &:hover {
      @include respond-to('desktop') {
        background-color: $btn-background-color;
        border: 3px solid $btn-background-color;
        color: $btn-primary-color;
        font-family: $btn-font-primary;
        opacity: $btn-passive-opacity;
        padding: 8px 30px;
      }
    }
  }

  @include respond-to('all-tablets') {
    font-size: $btn-font-size;
    height: 50px;
    padding: 6px 20px;
  }

  @include respond-to('desktop') {
    font-size: $btn-font-size;
    height: 50px;
    padding: 8px 30px;
  }

  // Default class for link button
  &--link {
    align-items: center;
    display: inline-flex;
    span {
      margin: 0 auto;
    }
  }

  // Modifier for block button
  &--block {
    font-size: $btn-font-size;
    height: 40px;
    width: 100%;

    @include respond-to('all-tablets') {
      height: 50px;
    }

    @include respond-to('desktop') {
      height: 50px;
    }
  }

  // Button icon style
  span + .icon-navigate-next {
    font-size: 12px;
    padding-left: 5px;
  }

  &--mobile {
    padding-left: 0;
    padding-right: 0;
  }
  &--grey-dark {
    background-color: $color-grey-dark;
  }
  &--orange-fade {
    background-color: $color-orange-fade;
  }
  &--white {
    background-color: $white;
  }
  &--disabled {
    pointer-events: none;
  }
  &--popin {
    align-items: center;
    display: flex;
    height: 100%;
    justify-content: center;
  }
  &--has-hover:hover {
    background-color: $white;
    border: 2px solid $form-color;
    color: $form-color;
  }
}

// For philosophy btn
@mixin btn-active-state($background-color, $border-color) {
  background-color: $background-color;
  border: 1px solid $border-color;
}

.rounded-button {
  align-items: center;
  background: $color-blue-darker;
  border: 2px solid $color-blue-darker;
  border-radius: 75px;
  color: $white;
  cursor: pointer;
  display: flex;
  font-size: 20px;
  height: 30px;
  justify-content: center;
  outline: none;
  width: 30px;
  z-index: 1;

  @include respond-to('desktop') {
    font-size: 40px;
    height: 60px;
    width: 60px;
  }
  &--bordered {
    border: 2px solid $white;
  }
}

.section__action-button {
  position: absolute;
  right: -5px;
  top: -7px;
  z-index: 1;

  @include respond-to('desktop') {
    right: -10px;
    top: -15px;
  }
}

.icon-reverse-state {
  font-size: 12px;
  padding-left: 5px;
  transform: rotate(180deg);
}

